@import '@radix-ui/colors/blackA.css';
@import '@radix-ui/colors/mauve.css';
@import '@radix-ui/colors/red.css';
@import '@radix-ui/colors/green.css';
@import '@radix-ui/colors/violet.css';

.dashboard-body {
    max-width: 1284px;
    margin-inline: auto;
    margin-top: 3rem;
    padding-inline: 1.315rem;
}

.dashboard-body-table {
    margin-block: 3rem;
    padding-bottom: 2rem;
    display: grid;
    gap: 1.5rem;
}

.user-table-form {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.btn[data-icon="add-icon"]::before {
    content: "";
    background-image: url("../../icons/plus.svg");  
    background-position: center;
    background-size: cover;
    width: 18px;
    aspect-ratio: 1;
}

.btn-add {
    padding-block: 0.4rem;
    background-color: var(--clr-primary);
    color: var(--clr-neutral-100);
}

.btn-delete, 
.btn-edit {
    padding-block: 0;
    background-color: var(--clr-neutral-100);
}

.btn-sign-out {
    padding: 0.4rem 0.6rem;
    font-size: 0.8rem;
    background-color: #F9F5FF;
    color: #6941C6;
}

.btn-save {
    background-color: var(--green4);
    color: var(--green11);
}

.btn-submit {
    background-color: var(--clr-primary);
    color: var(--clr-neutral-100);
}

.btn[data-icon="signout-icon"]::after {
    content: "";
    background-image: url("../../icons/exit.svg");  
    background-position: center;
    background-size: cover;
    width: 18px;
    aspect-ratio: 1;
}

.btn[data-icon="delete-icon"]::before {
    content: "";
    background-image: url("../../icons/delete.svg");  
    background-position: center;
    background-size: cover;
    width: 18px;
    aspect-ratio: 1;
}

.btn[data-icon="edit-icon"]::before {
    content: "";
    background-image: url("../../icons/edit.svg");  
    background-position: center;
    background-size: cover;
    width: 18px;
    aspect-ratio: 1;
}

/* Radix UI Alert Box */
.AlertDialogOverlay {
    background-color: var(--blackA9);
    position: fixed;
    inset: 0;
    animation: overlayShow 150ms cubic-bezier(0.16, 1, 0.3, 1);
}
  
.AlertDialogContent {
    background-color: white;
    border-radius: 6px;
    box-shadow: hsl(206 22% 7% / 35%) 0px 10px 38px -10px, hsl(206 22% 7% / 20%) 0px 10px 20px -15px;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 90vw;
    max-width: 500px;
    max-height: 85vh;
    padding: 25px;
    animation: contentShow 150ms cubic-bezier(0.16, 1, 0.3, 1);
}

.AlertDialogContent:focus {
    outline: none;
}
  
.AlertDialogTitle {
    margin-bottom: 0.5rem;
    color: var(--mauve12);
    font-size: 17px;
    font-weight: 500;
}
  
.AlertDialogDescription {
    margin-bottom: 20px;
    color: var(--mauve11);
    font-size: 15px;
    line-height: 1.5;
}
  
.Button {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    border-radius: 4px;
    padding: 0 15px;
    font-size: 15px;
    line-height: 1;
    font-weight: 500;
    height: 35px;
}

.Button.red {
    background-color: var(--red4);
    color: var(--red11);
}
  
  
@keyframes overlayShow {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
}
  
  
@keyframes contentShow {
    from {
      opacity: 0;
      transform: translate(-50%, -48%) scale(0.96);
    }
    to {
      opacity: 1;
      transform: translate(-50%, -50%) scale(1);
    }
}

/* Radix UI Edit Dialog */
.DialogOverlay {
    background-color: var(--blackA9);
    position: fixed;
    inset: 0;
    animation: overlayShow 150ms cubic-bezier(0.16, 1, 0.3, 1);
}
  
.DialogContent {
    background-color: white;
    border-radius: 6px;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 90vw;
    max-width: 500px;
    max-height: 95vh;
    padding: 25px;
    animation: contentShow 150ms cubic-bezier(0.16, 1, 0.3, 1);
}
  
.DialogTitle {
    margin: 0;
    font-weight: 500;
    color: var(--mauve12);
    font-size: 1rem;
}
  
  
.DialogDescription {
    margin: 10px 0 20px;
    color: var(--mauve11);
    font-size: 0.9rem;
    line-height: 1.5;
}
  
  
.Button {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    border-radius: 4px;
    padding: 0 15px;
    font-size: 15px;
    line-height: 1;
    font-weight: 500;
    height: 35px;
}
  
  
.Button.green {
    background-color: var(--green4);
    color: var(--green11);
}

.IconButton {
    font-family: inherit;
    border-radius: 100%;
    height: 25px;
    width: 25px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    color: var(--violet11);
    position: absolute;
    top: 10px;
    right: 10px;
}
    
.Fieldset {
    display: flex;
    gap: 20px;
    align-items: center;
    margin-bottom: 15px;
}
  
  
@keyframes overlayShow {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
}
  
  
@keyframes contentShow {
    from {
      opacity: 0;
      transform: translate(-50%, -48%) scale(0.96);
    }
    to {
      opacity: 1;
      transform: translate(-50%, -50%) scale(1);
    }
}

/* Dropdown Select */
.SelectTrigger {
  min-width: 342px;
  margin-bottom: 20px;
  display: inline-flex;
  align-items: center;
  justify-content: space-between;
  border-radius: 0.5rem;
  padding: 0.5rem 0.8rem;
  font-size: 1rem;
  line-height: 1;
  height: 35px;
  gap: 5px;
  background-color: white;
  color: var(--clr-text-light);
  border: 1px solid var(--clr-border)
}


.SelectTrigger:hover {
  background-color: var(--mauve3);
}

.SelectTrigger[data-placeholder] {
  color: var(--clr-text-dark);
}

.SelectIcon {
  color: Var(--violet11);
}

.SelectContent {
  overflow: hidden;
  background-color: white;
  border-radius: 6px;
  box-shadow: 0px 10px 38px -10px rgba(22, 23, 24, 0.35), 0px 10px 20px -15px rgba(22, 23, 24, 0.2);
}

.SelectViewport {
  padding: 5px;
}

.SelectItem {
  font-size: 15px;
  line-height: 1;
  color: var(--clr-text-dark);
  border-radius: 3px;
  display: flex;
  align-items: center;
  height: 25px;
  padding: 0 35px 0 25px;
  position: relative;
  user-select: none;
}
.SelectItem[data-disabled] {
  color: var(--mauve8);
  pointer-events: none;
}
.SelectItem[data-highlighted] {
  outline: none;
  background-color: var(--violet9);
  color: var(--violet1);
}

.SelectLabel {
  padding: 0 25px;
  font-size: 12px;
  line-height: 25px;
  color: var(--mauve11);
}


.SelectItemIndicator {
  position: absolute;
  left: 0;
  width: 25px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
}

.SelectScrollButton {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 25px;
  background-color: white;
  color: var(--violet11);
  cursor: default;
}
  


